<template>
    <div id="single-blog">
      <h3>{{blog.title}}</h3>
      <p>{{blog.content}}</p>
      <p>作者：{{blog.author}}</p>
      分类：
      <ul>
        <li v-for="item in blog.catagories">{{item}}</li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "single-blog",
      data(){
          return{
            id:this.$route.params.id,
            blog:{}
          }
      },
      created(){
          this.$http.get("https://myblog-5e807.firebaseio.com/posts/"+this.id+".json")
            .then(function (data) {
              // console.log(data);
              this.blog=data.body;
            })
      }
    }
</script>

<style scoped>
  #single-blog {
    max-width: 960px;
    padding: 20px;
    margin: 0 auto;
    background: #eee;
    border: 1px dotted #ccc;
  }
</style>



