<template>
  <!--传参改变背景色-->
  <!--<div v-theme:theme="'wide'" id="show-blog">-->
  <div v-theme="'wide'" id="show-blog">
    <h1>博客总览</h1>
    <input type="text" placeholder="搜索" v-model="search">
    <div v-for="blog in filteredBlogs" class="single-blog">
      <router-link :to="'/blog/'+blog.id"><h3 v-color>{{blog.title }}</h3></router-link>
      <article>{{blog.content}}</article>
    </div>
  </div>
</template>

<script>
  export default {
    name: "show-blog",
    data() {
      return {
        blogs: [],
        search: "",
      }
    },
    created() {
      this.$http.get("https://myblog-5e807.firebaseio.com/posts.json")
        .then(function (data) {
          return data.json();
        }).then(function (data) {
        var blogsArry = [];
        for (let key in data) {
          data[key].id = key;
          blogsArry.push(data[key]);
        }
        this.blogs = blogsArry;
      })
    },
    computed: {
      filteredBlogs() {
        return this.blogs.filter((blog) => {
          return blog.title.match(this.search);
        })
      }
    }
    // filters: {
    //   upper(value) {
    //     return value.toUpperCase();
    //   }
    // },
    // updated() {
    //     console.log(this.blogs);
    //     console.log(this.search);
    // }

  }
</script>

<style scoped type="text/less" lang="less">
  #show-blog {
    max-width: 800px;
    margin: 0 auto;
    input {
      padding: 8px;
      width: 100%;
      margin-top: 20px;
    }
    .single-blog {
      padding: 20px;
      margin: 20px 0;
      background: #eee;
      border: 1px dotted #ccc;
      a {
        text-decoration: none;
      }
    }
  }
</style>



